.column {
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 40px;
    align-items: center;
}

.row {
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
}

.inputs {
    margin-top: 100px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 40px;
    align-items: center;
}

.wordart {
  font-family: Arial, sans-serif;
  font-size: 4em;
  font-weight: bold;
  position: relative;
  z-index: 1;
  display: inline-block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.wordart.rainbow {
  transform: scale(1, 1.5);
  -webkit-transform: scale(1, 1.5);
  -moz-transform: scale(1, 1.5);
  -o-transform: scale(1, 1.5);
  -ms-transform: scale(1, 1.5);
}

.wordart.rainbow .text {
  background: red;
  background: -webkit-linear-gradient(
    left,
    #b306a9,
    #ef2667,
    #f42e2c,
    #ffa509,
    #fdfc00,
    #55ac2f,
    #0b13fd,
    #a804af
  );
  background: -o-linear-gradient(
    left,
    #b306a9,
    #ef2667,
    #f42e2c,
    #ffa509,
    #fdfc00,
    #55ac2f,
    #0b13fd,
    #a804af
  );
  background: -moz-linear-gradient(
    left,
    #b306a9,
    #ef2667,
    #f42e2c,
    #ffa509,
    #fdfc00,
    #55ac2f,
    #0b13fd,
    #a804af
  );
  background: linear-gradient(
    to right,
    #b306a9,
    #ef2667,
    #f42e2c,
    #ffa509,
    #fdfc00,
    #55ac2f,
    #0b13fd,
    #a804af
  );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.wordart.superhero {
    transform: skew(0, -15deg) scale(1, 1.5);
    -webkit-transform: skew(0, -15deg) scale(1, 1.5);
    -moz-transform: skew(0, -15deg) scale(1, 1.5);
    -o-transform: skew(0, -15deg) scale(1, 1.5);
    -ms-transform: skew(0, -15deg) scale(1, 1.5);
}

